html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.swal2-popup {
  font-size: 1.5rem !important;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 3rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 4rem;
  }
}

h2 {
  font-size: 2.6rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 3.6rem;
  }
}

h3 {
  font-size: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  h3 {
    font-size: 3rem;
  }
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
  padding: 0 3rem;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .nombre-pagina {
    font-size: 5rem;
  }
}

.descripcion-pagina {
  text-align: center;
  font-size: 1.7rem;
  margin-bottom: 2rem;
  padding: 0 3rem;
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  .descripcion-pagina {
    font-size: 1.6rem;
  }
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    gap: 1fr;
    grid-template-columns: repeat(2, 1fr);
    height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 20rem;
  background-image: url(../img/1.webp);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  width: 100%;
  margin: 0 auto;
  padding: 3rem 0;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
    overflow-x: hidden;
    padding-right: 1.5rem;
    padding-left: 1.5rem;
  }
}
.contenedor-app .app::-webkit-scrollbar {
  width: 0.9rem;
}
.contenedor-app .app::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 5px;
}
.contenedor-app .contenedor-principal-usuario {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 2rem;
  height: 10rem;
}
.contenedor-app .contenedor-principal-usuario .usuario-data {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.seccion {
  display: none;
  margin: 5rem 0;
}
.seccion .text-center {
  text-align: center;
}
.seccion p {
  font-size: 1.6rem;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-servicios {
  margin: 4rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 1fr;
    grid-template-columns: repeat(2, 1fr);
    margin: 4rem 1rem;
  }
}
.listado-servicios .servicio {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #FFFFFF;
  border-radius: 1rem;
  height: 10rem;
  width: 100%;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .listado-servicios .servicio {
    height: 12rem;
  }
}
.listado-servicios .servicio:hover {
  transform: scale(1.02);
  cursor: pointer;
  box-shadow: 0px 0px 5px 4px #0da6f3;
}
.listado-servicios .servicio .nombre-servicio {
  color: #1a1b15;
  font-size: 1.7rem;
  margin: 0 0 1rem 0;
  width: 95%;
}
.listado-servicios .servicio .precio-servicio {
  color: #0da6f3;
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0;
}
.listado-servicios .seleccionado {
  background-color: #0da6f3;
  transform: scale(0.95);
}
.listado-servicios .seleccionado .nombre-servicio {
  color: #FFFFFF;
}
.listado-servicios .seleccionado .precio-servicio {
  color: #FFFFFF;
}
.listado-servicios .seleccionado:hover {
  transform: scale(0.95);
  box-shadow: none;
}

#citas-admin .citas {
  list-style-type: none;
  padding: 0 2rem;
}
#citas-admin .citas .citas-li {
  border-bottom: 1px solid #e1e1e1;
  padding: 2.5rem 0;
}
#citas-admin .citas .citas-li:last-child {
  border: none;
}
#citas-admin .citas .citas-li p {
  color: #0da6f3;
  font-weight: 700;
}
#citas-admin .citas .citas-li p span {
  color: #FFFFFF;
  font-weight: 100;
}

.formulario {
  padding: 0 3rem;
}
.formulario .campo-contenedor {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 4rem 0;
}
.formulario .campo-contenedor .campo label {
  display: block;
  margin: 1rem 0;
  font-weight: bold;
}
.formulario .campo-contenedor .campo input {
  width: 100%;
  height: 4.5rem;
  padding: 1.3rem;
  border-radius: 1rem;
  font-size: 1.4rem;
}
.formulario .campo-contenedor .campo input:disabled {
  background-color: #404040;
  color: white;
  cursor: not-allowed;
  user-select: none;
}

.acciones {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2rem 3rem;
  gap: 1.5rem;
  font-size: 1.2rem;
}
@media (min-width: 768px) {
  .acciones {
    font-size: 1.6rem;
  }
}
.acciones p {
  margin: 0;
  font-size: 1.2rem;
}
@media (min-width: 768px) {
  .acciones p {
    font-size: 1.6rem;
  }
}
.acciones a {
  color: rgb(245, 244, 244);
}
.acciones a:hover {
  color: rgb(161, 160, 160);
}

.btn-azul {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 3rem 0;
  width: 100%;
  padding: 1.2rem;
  border: none;
  cursor: pointer;
  background-color: #0da6f3;
  color: #FFFFFF;
  font-weight: 700;
  border-radius: 1rem;
  transition: all 0.3s ease-in-out;
}
.btn-azul:hover {
  transform: scale(0.985);
  background-color: #0a85c3;
}

.btn-azul-chico {
  background-color: #0da6f3;
  padding: 1.1rem 3rem;
