  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .btn-azul-chico {
    width: auto;
