.alerta {
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  color: #FFFFFF;
  padding: 1rem;
  box-shadow: 2px 2px 5px black;
  font-size: 1.1rem;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}
@media (min-width: 768px) {
  .alerta.error, .alerta.exito {
    font-size: 1.3rem;
  }
}

.tabs {
  display: flex;
